<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
  <!--此处需将toolbar的高度留出-->
  <v-app class="mt-1">
    <v-toolbar app height="32" class="white mb-4">
        <!--<v-btn flat style="cursor: pointer" class="pt-0" @click="goHome">-->
          <!--<v-img height="2rem" width="2rem" contain aspect-ratio="1" src="./static/image/logo-90.png"/>-->
          <!--<span class="ml-1 hidden-xs-only">-->
                        <!--babyhard.com-->
                      <!--</span>-->
        <!--</v-btn>-->

      <v-layout justify-center align-center row class="text-uppercase font-weight-bold">
        <v-card flat tile ripple class="pa-1" style="cursor: pointer" @click="goHome">BliFace</v-card>
        <v-card flat tile ripple class="pa-1 mr-2" style="cursor: pointer" @click="gitHub">GitHub</v-card>
        <!--此处固定显示logoTag.png 可根据不同节气将节日logo改为logoTag显示，默认显示logo-90-->
        <!--gitHub icon: https://raw.githubusercontent.com/bliface/video/master/babyhard-icon.png-->
        <v-img style="border-radius: 19px; border:1px solid orangered; cursor: pointer" max-height="27" max-width="27" aspect-ratio="1" contain src="./static/image/logoTag.png" @click="viewLogoTag"/>
        <v-card flat tile ripple class="pa-1 ml-2" style="cursor: pointer" @click="ssCode">Code</v-card>
        <v-card flat tile ripple class="pa-1" style="cursor: pointer" @click="goUploadFile">Upload</v-card>
      </v-layout>

        <!--<v-btn flat @click="goHome">BliFace</v-btn>-->
        <!--<v-btn flat @click="gitHub">GitHub</v-btn>-->
        <!--<v-btn flat @click="ssCode">SourceCode</v-btn>-->
        <!--<v-btn flat class="hidden-xs-only" @click="goSSCoin">SS Coin</v-btn>-->
        <!--<v-btn flat class="hidden-xs-only" @click="ssWhiteBook">WhiteBook</v-btn>-->
        <!--<v-btn flat class="hidden-xs-only" @click="goUploadFile">Upload</v-btn>-->

      <v-menu justify-end bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            icon
            v-on="on"
            color="while"
          >
            <v-icon color="blue-grey">more_vert</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-tile @click="gitHub">GitHub</v-list-tile>
          <v-list-tile @click="goUploadFile">Upload Video</v-list-tile>
          <v-list-tile @click="goSSCoin">SS Coin</v-list-tile>
          <v-list-tile @click="ssCode">SourceCode</v-list-tile>
          <v-list-tile @click="ssWhiteBook">WhiteBook</v-list-tile>
          <v-list-tile @click="goManage">Manage</v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>

    <!--<v-content class="pa-0 white" style="background-image: url(../static/image/nature-3054797_1920.jpg)">-->
    <v-content class="pt-2 mt-4 white">
    <router-view class="pt-0" :key="$router.fullPath"/>
      <!--缓冲页面用于在移动端按返回键可以回到前一页 不刷新-->
      <!--<keep-alive>-->
        <!--<router-view v-if="$route.meta.keepAlive">-->
          <!--&lt;!&ndash; 这里是会被缓存的视图组件，比如 page1,page2 &ndash;&gt;-->
          <!--<HelloWorld/>-->
        <!--</router-view>-->
      <!--</keep-alive>-->

      <!--<router-view v-if="!$route.meta.keepAlive">-->
        <!--&lt;!&ndash; 这里是不被缓存的视图组件，比如 page3 &ndash;&gt;-->
        <!--<Detail/>-->
      <!--</router-view>-->

    </v-content>
    <!--分割线-->
    <!--<v-divider></v-divider>-->
    <!--<v-footer class="pa-3">-->
      <!--<v-layout-->
        <!--justify-center-->
        <!--row-->
      <!--&gt;-->
        <!--<span>-->
          <!--<v-layout row>-->
            <!--<v-img height="2rem" width="2rem" src="./static/image/logo-90.png"/>-->
            <!--<span class="mt-1 ml-2">ETHEREUM + IPFS &copy; {{ new Date().getFullYear() }}-babyhard.com</span>-->
            <!--<a href="https://github.com/babyhard/video" target='_blank'>-->
              <!--<v-img height="1.5rem" width="1.5rem" class="ml-3 mt-1" src="./static/image/githubico.jpg"/>-->
            <!--</a>-->
          <!--</v-layout>-->
        <!--</span>-->

      <!--</v-layout>-->
    <!--</v-footer>-->
    <!--在屏幕中间显示logoTag大图-->
    <v-dialog v-model="showBigLogoTag" hide-overlay persistent width="99">
      <v-img max-height="99" max-width="99" aspect-ratio="1" style="border-radius: 9px; border:1px solid orangered;" contain src="./static/image/logoTag.png"/>
    </v-dialog>
  </v-app>
  <!--<div id="app">-->
    <!--<img src="./assets/logo.png">-->
    <!--<router-view/>-->
  <!--</div>-->
</template>

<script>
import HelloWorld from './components/HelloWorld'
import Detail from './components/Detail'
export default {
  name: 'App',
  components: {Detail, HelloWorld},
  data: () => ({
    showBigLogoTag: false
  }),
  mounted () {
  },
  methods: {
    viewLogoTag () {
      if (this.showBigLogoTag) {
        this.showBigLogoTag = false
        return
      }

      this.showBigLogoTag = true
      let that = this
      // 三秒后关闭提示
      setTimeout(function () {
        that.showBigLogoTag = false
      }, 1000 * 5)
    },
    goHome () {
      let url = 'https://bliface.com'

      if (window.location.protocol === 'http:') {
        url = 'http://bliface.com'
      }
      window.open(url)

      // this.$router.push({path: '/'})
    },
    goUploadFile () {
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})

      let toPage = '/uploadFile'
      // 同一页面不跳转
      if (window.location.href.indexOf(toPage) > 0) {
        return
      }

      if (window.location.protocol === 'https:') {
        alert('upload video need http Protocol\nplease use http://bliface.com')
        return
      }

      // 上传界面在https协议下会报错 强制修改为http协议中进行 （需在服务器端改善)
      this.$router.push({path: toPage})

      console.log('go: ', toPage)
    },
    goManage () {
      let toPage = '/Manage'
      this.$router.push({path: toPage})
    },
    goSSCoin () {
      let toPage = '/coinMarket'
      this.$router.push({path: toPage})
      // // 同一页面不跳转
      // if (window.location.href.indexOf(toPage) > 0) {
      //   return
      // }
      //
      // let routeData = this.$router.resolve({
      //   path: toPage
      // })
      //
      // window.open(routeData.href) // 新窗口打开  ,'_blank'
      console.log('go: ', toPage)
    },
    ssCode () {
      let url = 'https://github.com/bliface/video/blob/master/SS%20Source%20Code.sol'
      window.open(url)
    },
    ssWhiteBook () {
      let url = 'https://github.com/bliface/video/blob/master/ss%20whitebook.pdf'
      window.open(url)
    },
    gitHub () {
      let url = 'https://github.com/bliface/video'
      window.open(url)
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



