<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
  <v-container style="max-width: 80rem">
    <v-layout justify-center>
      <v-flex xs12>
        <!--context-->
        <!--空video用于防视频被嗅探-->
        <!--<v-layout justify-center wrap row width="100%">-->
        <!--<video width="100%" v-show="this.video.pm" id="player" ref="player" controls="controls">-->
        <!--<source src= "movie.mp4" type="video/mp4">-->
        <!--<source src= "movie.ogg" type="video/ogg">-->
        <!--</video>-->
        <!--</v-layout>-->

        <!--vue video player播放器-->
        <video-player  class="vjs-big-play-centered blue-grey"
                       width="100%"
                       id="videoPlayer"
                       ref="videoPlayer"
                       :options="playerOptions"
                       @play="onPlayerPlay($event)"
                       @pause="onPlayerPause($event)"
                       @ended="onPlayerEnded($event)"
                       @loadeddata="onPlayerLoadeddata($event)"
                       @waiting="onPlayerWaiting($event)"
                       @playing="onPlayerPlaying($event)"
                       @timeupdate="onPlayerTimeupdate($event)"
                       @canplay="onPlayerCanplay($event)"
                       @canplaythrough="onPlayerCanplaythrough($event)"
                       @ready="playerReadied"
                       @statechanged="playerStateChanged($event)">
        </video-player>

        <!--xgplayer播放器占位标签-->
        <!--<div id="vueMse" ref="vueMse"/>-->
        <v-layout text-xs-left class="blue-grey white--text pa-2">
          <v-flex>
            <h4 class="font-weight-regular">{{playerVideoInfo.title}}</h4>
            <h5 class="font-weight-light">{{playerVideoInfo.tags}}</h5>
            <v-layout align-end row>
              <v-icon small color="white">visibility</v-icon>
              <h6 class="pt-2 ml-1 font-weight-light">{{playerVideoInfo.viewCount}}</h6>
              <v-icon small color="white" class="ml-3">thumb_up</v-icon>
              <h6 class="pt-2 ml-1 font-weight-light ">{{playerVideoInfo.praise}}</h6>
            </v-layout>
          </v-flex>
          <v-spacer></v-spacer>
          <v-layout justify-end text-xs-right row >
            <v-flex >
              <!--<v-label>{{this.playerVideoInfo.praise}}</v-label>-->
              <v-tooltip v-model="praiseTip" bottom>
                <template v-slot:activator="{ on }">
                  <v-btn justify-end id="VMPraise" ref="VMPraise" round flat color="white" @click="addPraise">
                    <!--文本插值不能加this-->
                    <v-icon right large>thumb_up</v-icon>
                  </v-btn>
                </template>
                <h3 class="font-weight-regular">{{this.praiseTipText}}</h3>
              </v-tooltip>
            </v-flex>
          </v-layout>
        </v-layout>

        <!--显示视频推荐列表-->
        <v-layout row wrap class="mt-3">
          <v-flex xs12 sm6
                  v-for="(item,index) in this.$store.state.videoRecommendList" :key="index">
            <v-card v-ripple class="mt-2" @click="player(item)">
              <v-layout text-xs-left row class="body-1">
                <v-flex xs4>
                  <!--<v-img src="/static/image/t1.jpg"/>-->
                  <v-img aspect-ratio="1.3"
                         align-center justify-start
                         :src="`${item.imgPath}`"
                         :lazy-src="`${item.imgPath}`">

                    <!--<v-layout fill-height align-end white&#45;&#45;text>-->
                                <!--<span class="ml-1">-->
                                  <!--<v-icon small color="white" class="halfTrans">thumb_up</v-icon>-->
                                  <!--{{item.praise}}-->
                                <!--</span>-->
                    <!--</v-layout>-->
                  </v-img>
                </v-flex>
                <v-flex xs7 class="ml-2 mt-1">
                  <v-layout>
                    <h3 class="font-weight-regular">{{item.title}}</h3>
                  </v-layout>
                  <v-layout>
                    <h4 class="font-weight-light">{{item.tags}}</h4>
                  </v-layout>
                  <v-layout>
                    <h6 class="font-weight-light">{{item.time}}</h6>
                  </v-layout>
                  <v-layout align-end row class="text--lighten-5">
                    <v-icon small color="grey lighten-1">visibility</v-icon>
                    <h6 class="pt-1 ml-1 font-weight-light">{{item.viewCount}}</h6>
                    <v-icon small color="grey lighten-1" class="ml-3">thumb_up</v-icon>
                    <h6 class="pt-1 ml-1 font-weight-light">{{item.praise}}</h6>
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-card>
          </v-flex>
        </v-layout>

        <!--comments-->
        <v-flex xs12 class="mt-3">
          <v-layout class="mt-5">
            <v-text-field label="CHAT 32 byte" maxLength="32" hide-details append-icon="edit" v-model="inputDiscuss"/>
          </v-layout>

          <v-layout justify-end class="mt-2 mr-2">
            <v-btn flat dark icon color="blue" @click="anonymousClick">
              <v-icon color="black">how_to_reg</v-icon>
            </v-btn>
            <v-flex xs6 v-if="anonymousShow">
              <v-text-field ref='VMNickname' label="user nickname" maxLength="32" hide-details v-model="inputNickName"/>
            </v-flex>
            <v-flex xs1>
              <v-layout>
                <v-btn dark icon class="white--text" color="blue-grey" @click="summitChat">
                  <v-icon>cloud_upload</v-icon>
                </v-btn>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-flex>

        <v-list two-line class="mt-3 mb-3">
          <v-subheader>Comments</v-subheader>
          <v-divider></v-divider>
          <template v-for="(item, index) in this.discussSet">
            <v-list-tile :key="index" avatar>
              <v-list-tile-content>
                <v-list-tile-title >{{ item.context }} </v-list-tile-title>
                <v-list-tile-sub-title class="mt-2">{{ item.nickname }}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider :key="item.time"></v-divider>
          </template>
        </v-list>
      </v-flex>
    </v-layout>
    <!--在屏幕中间显示 共用等待框-->
    <v-dialog v-model="this.$store.state.waitDialog" hide-overlay persistent width="210">
      <v-card color="primary" dark>
        <v-card-text>
          Please stand by
          <v-progress-circular
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-circular>
        </v-card-text>
      </v-card>
    </v-dialog>

  </v-container>
</template>

<script>
import contractABI from '../js/Ethereum'
import { videoPlayer } from 'vue-video-player'
require('video.js/dist/video-js.css')

export default {
  name: 'Detail',
  components: {videoPlayer},
  data: () => ({
    // 变量
    playerVideoInfo: {},
    inputNickName: '',
    inputDiscuss: '',
    anonymousShow: false,
    praiseTip: false,
    video: {pm: false, src1: '', src2: ''},
    praiseTipText: '',
    discussSet: [],
    // vvp
    // defaultImg: '/static/image/logo-200.png',
    playerOptions: {}
  }),
  watch: {
    inputNickName: function () {
      // 长度不超32
      if (this.inputNickName !== null) {
        this.inputNickName = this.$global.subStringByte(this.inputNickName, 32)
      }
    },
    inputDiscuss: function () {
      // 长度不超32
      if (this.inputDiscuss !== null) {
        this.inputDiscuss = this.$global.subStringByte(this.inputDiscuss, 32)
      }
    }
  },
  mounted () {
    this.$store.state.waitDialog = true // 先加载等待框

    // 加载时 接收主参数 初始化视频信息
    if (typeof this.$route.query.key !== 'undefined') {
      let key = this.$route.query.key
      this.playerVideoInfo = JSON.parse(localStorage.getItem(key))

      this.player(this.playerVideoInfo)
    } else {
      // 如果没有token参数就用系统默认token
      alert('未能获得视频hash值 ')
    }

    // 加载用户名
    this.inputNickName = localStorage.getItem('nickname')

    this.$store.state.waitDialog = false // 闭关等待框

    // 加载推荐视频
    contractABI.initVideoListForRecommendList(this.playerVideoInfo, 6)
  },
  methods: {

    // 双向开关控制是否显示用户昵称
    anonymousClick () {
      if (this.anonymousShow) {
        this.anonymousShow = false
      } else {
        this.anonymousShow = true
      }
    },

    // 上传讨论
    summitChat () {
      if (this.inputDiscuss === '') {
        alert('please input chat content')
        return
      }

      // 检测是否重复发贴
      if (this.discussSet.length > 0) {
        for (let i = 0; i < this.discussSet.length; i++) {
          if (this.discussSet[i].context === this.inputDiscuss) {
            alert('Do not repeat the reply')
            return
          }
        }
      }

      let hashByte = this.playerVideoInfo.videoHash
      let user = this.$store.state.myAccount
      let nickNameByte = ''
      let chat = this.inputDiscuss

      if (this.inputNickName !== null) {
        nickNameByte = this.inputNickName
        // 保存用户名
        localStorage.setItem('nickname', this.inputNickName)
      }

      let discussItem = {
        'user': user,
        'nickname': nickNameByte,
        'context': chat,
        'praise': '0'
      }
      console.log('ChatContext1', user)
      console.log('ChatContext2', hashByte, user, nickNameByte, chat)

      this.discussSet.unshift(discussItem)

      contractABI.pushDiscuss(hashByte, user, nickNameByte, chat)
    },

    getVideoDiscuss (_videoHash) {
      const hashByte = contractABI.hashToByte32(_videoHash)
      const fileInfoABI = contractABI.initContract(this.$store.state.sysModel)
      let that = this
      fileInfoABI.getVideoDiscussLength(hashByte)
        .then(function (response1) {
          // console.log('discussCount', response1, response1[0].toString())

          let discussCount = parseInt(response1[0].toString())

          if (discussCount > 0) {
            for (let i = 0; i < discussCount; i++) {
              fileInfoABI.getVideoDiscuss(hashByte, i)
                .then(function (response2) {
                  let user = response2[0].toString()
                  let nickname = contractABI.byte32ToStr(response2[1])
                  let context = contractABI.byte32ToStr(response2[2])
                  let praise = response2[3].toString()
                  let time = response2[4].toString()

                  // console.log('res2', response2, i, response2[1].indexOf('000'), '\nreplace', response2[1].substring(0, response2[1].indexOf('000')), response2[1].replace('000', ''), nickname.length, that.discussSet.length)

                  let discussItem = {
                    'user': user,
                    'nickname': nickname,
                    'context': context,
                    'praise': praise,
                    'time': time
                  }
                  that.discussSet.unshift(discussItem)
                  // console.log('getVideoDiscuss', response2, i, user, nickname, context, praise, time, that.discussSet.length)
                })
            }
          }
        })
        .catch(function (error) {
          console.log('getVideoDiscuss error', error)
        })
    },
    // 点赞
    addPraise () {
      // console.log('this.praise', this.praise, this.$store.getters.getHomeVideoList('fdfsf'))
      let praiseKey = 'praise' + this.playerVideoInfo.videoHash
      console.log(localStorage.getItem(praiseKey))
      // 不能重复点赞
      if (localStorage.getItem(praiseKey) === null || localStorage.getItem(praiseKey) === '0') {
        this.praiseTipText = 'Thank You\n ' + this.playerVideoInfo.praise.toString() + ' + 1'
        this.playerVideoInfo.praise = parseInt(this.playerVideoInfo.praise) + 1
        // 本地记录点赞历史
        localStorage.setItem(praiseKey, '1')
        // 写入区块链
        contractABI.pushPraise(this.playerVideoInfo.videoHash)
      } else {
        this.praiseTipText = 'Already Praised'
      }
      this.praiseTip = true
      let that = this
      // 三秒后关闭提示
      setTimeout(function () {
        that.praiseTip = false
      }, 1000 * 5)
    },

    player (item) {
      let gateway = this.$store.state.myGateway
      let videoHashFromByte = contractABI.byte32ToHash(item.videoHash)
      // 后台密钥
      let argExpire = parseInt((new Date().getTime() / 1000)) + parseInt((24 * 3600 * 1000 * 2))
      let argST = contractABI.getSecretMd5(gateway + videoHashFromByte + argExpire.toString())

      // let ddd = contractABI.getSecretMd52(argExpire)
      let videoUrl = gateway + videoHashFromByte + '?st=' + argST + '&e=' + argExpire
      // let videoUrl = this.$store.state.myGateway + videoHash
      let videoPoster = gateway + item.imgHash

      // 调用播放器需事先定义界面标签
      this.VPplayer(videoUrl, videoPoster) // vue video player
      // this.Gplayer(videoUrl, videoPoster) // 西瓜
      // 加载视频讨论
      this.getVideoDiscuss(videoHashFromByte)

      window.scrollTo(0, 0) // // 滚动到顶部位置
      // document.getElementById('videoPlayer').scrollIntoView() // 滚动到某个标签

      // 重置当前视频信息
      this.playerVideoInfo = item

      console.log('videoInfo', item, videoHashFromByte)
    },

    // 加载 vue video player播放器
    VPplayer (videoUrl, videoPoster) {
      this.playerOptions = {
        notSupportedMessage: '此视频暂无法播放，请稍后再试',
        fluid: true,
        preload: 'auto',
        language: 'en',
        playbackRates: [0.5, 1, 2, 3],
        poster: videoPoster,
        sources: [{
          type: 'video/mp4',
          src: videoUrl
        }],
        // 按钮控制条
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true // 全屏按钮
        }
      }
    },

    /**
       * 加载西瓜播放器
       * @param videoUrl
       * @constructor
       */
    // Gplayer (videoUrl, videoPoster) {
    //   let gplayer = new Gplayer({
    //     id: 'vueMse', // player界面控件
    //     url: videoUrl,
    //     poster: videoPoster,
    //     videoInit: true, // 在没有视频封面的时候用视频首帧做封面
    //     fluid: true, // 流式布局 随父元素的宽度大小变化
    //     playsinline: true, // 内联模式 在手机观看时，开启ios和微信的内联模式
    //     playbackRate: [0.5, 1, 2, 3], // 陪速选项  defaultPlaybackRate: 1.0, // 设置默认陪速
    //     enterLogo: { // 视频加载页logo [待定]
    //       url: '/static/image/logo-45.png',
    //       width: 45,
    //       height: 45
    //     }
    //   })
    //
    //   console.log('videoControl', gplayer)
    //   // playerConfig.play()
    // },

    // 判断横竖屏
    renderResize () {
      // 判断横竖屏
      let width = document.documentElement.clientWidth
      let height = document.documentElement.clientHeight
      // let video = this.$refs.videoPlayer
      if (width > height) {
        // 横屏
        // console.log('width > height')
      }
    },
    // 手机返回健执行
    async goBack () {
      // this.$router.push({'name': 'HelloWorld'})
      // let toPage = '/'
      // // 同一页面不跳转
      // if (window.location.href.indexOf(toPage) > 0) {
      //   return
      // }
      //
      // let toRoute = this.$router.resolve({
      //   path: toPage
      // })
      //
      // // 闭关当前页
      // let userAgent = navigator.userAgent
      //
      // alert('phone go back ' + userAgent)
      //
      // if (userAgent.indexOf('Firefox') !== -1 || userAgent.indexOf('Chrome') !== -1) {
      //   window.location.href = toRoute.href
      // } else if (userAgent.indexOf('Android') > -1 || userAgent.indexOf('Linux') > -1) {
      //   window.opener = null
      //   window.open(toRoute.href, '_self').close()
      // } else {
      //   window.opener = null
      //   window.open(toRoute.href, '_self')
      //   window.close()
      // }

      let time1 = Date.now()

      // 2秒后关闭提示
      await this.$global.sleep(1000 * 2)
      let time2 = Date.now()

      alert('go back' + time1 + 'time' + time2)
    },

    // vue video player
    onPlayerPlay (player) {
      // console.log('player play!', player)
    },
    onPlayerPause (player) {
      // console.log('player pause!', player)
    },
    onPlayerEnded (player) {
      // console.log('player ended!', player)
    },
    onPlayerLoadeddata (player) {
      // console.log('player Loadeddata!', player, this.playerOptions)
    },
    onPlayerWaiting (player) {
      // console.log('player Waiting!', player)
    },
    onPlayerPlaying (player) {
      // console.log('player Playing!', player)
    },
    onPlayerTimeupdate (player) {
      // console.log('player Timeupdate!', player.currentTime())
    },
    onPlayerCanplay (player) {
      // console.log('player Canplay!', player)
    },
    onPlayerCanplaythrough (player) {
      // console.log('player Canplaythrough!', player)
    },
    // or listen state event
    playerStateChanged (playerCurrentState) {
      // console.log('player current update state', playerCurrentState)
    },
    // player is ready
    playerReadied (player) {
      // seek to 10s

      // console.log('example player 1 readied', player)
      player.currentTime(10)
      // console.log('example 01: the player is readied', player)
    }
  },
  // 组件结束执行
  destroyed () {
    console.log('log out')
    // 关闭手机物理按键监听
    // window.removeEventListener('popstate', this.goBack, false) // false阻止默认事件

    // 移除横屏监听
    // window.removeEventListener('resize', this.renderResize, false)
  }

}
</script>

<style scoped>

</style>



